@font-face {
  font-family: 'Title';
  src: url(/Marvel-ReactJS-API/static/media/palooka-bb.regular.b1c82c90.ttf);
  font-weight: bold;
  font-style: normal;
}
html, body {
  border: 0;
  margin: 0;
  padding: 0;
  height: 100%;
  line-height: 1.5;
  font-family: 'Roboto', 'sans-serif';
}
div, span, object, iframe, img, table, caption, thead, tbody,
tfoot, tr, tr, td, article, aside, canvas, details, figure, hgroup, menu,
nav, footer, header, section, summary, mark, audio, video {
  border: 0;
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cit, code,
del, dfn, em, ins, q, samp, small, strong, sub, sup, b, i, hr, dl, dt, dd,
ol, ul, li, fieldset, legend, label {
  border: 0;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}
article, aside, canvas, figure, figure img, figcaption, hgroup,
footer, header, nav, section, audio, video {
  display: block;
}
.app__heading {
    text-transform: uppercase;
    text-align: center;
}

.card-title {
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    text-align: left;
}
.menu {
    display: flex;
    justify-content:  center;
    align-items:  center;
    height: 100vh;
    font-family: 'Title', 'Roboto', 'sans-serif';
}

.menu__list {
    display:  flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width:  50%;
    font-size: 18px;
}

.menu__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 33%;
    margin-bottom: 50px;
    border: 2px solid #000000;
    transition: all 0.15s ease-in-out;
}

.menu__item:hover {
    border-color: #f0141e;
}
.menu__item:hover .menu__link {
    color: #f0141e;
    background-color: transparent;
}

.menu__link {
    height: 100%;
    width: 100%;
    text-align: center;
    color: #000000;
    font-size: 1.5rem;
}
.Navbar_app__nav__1Im2Z {
    display: flex;
    justify-content: space-around;
    background-color: #f0141e;
}
.main {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.main__header {
    text-transform: uppercase;
}
.loader {
    position: relative;
    width: 50px;
    height: 50px;
    margin: 0 auto;
}

.loader:before,
.loader:after {
    display: block;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 5px solid gray;
}

.loader:after {
    -webkit-animation: load 1s linear infinite;
            animation: load 1s linear infinite;
    border-color: red;
    border-left-color: transparent
}

@-webkit-keyframes load {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.card-main {
    padding: 10px;
    text-align: center;
}

.character__image {
    width: 300px;
    height: 300px;
}

.character__image img {
    width: 100%;
}

.character__actions a {
    margin-right: 10px;
}

.character__actions a:last-of-type {
    margin-right: 0;
}
.overlay {
    position: fixed;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
}

.popup {
    width: 300px;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.popup__close {
    cursor: pointer;
}
.footer {
    /* position: fixed;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%); */
    text-align: center;
    font-weight: bold;
    font-size: 11px;
}
